/* Poppins all styles */
@import url('https://fonts.googleapis.com/css2?family=Bungee+Inline&family=Orbitron:wght@400;600;700&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
@tailwind base;
@tailwind components;
@tailwind utilities;
@layer base {
    h1 {
        @apply text-2xl;
        color: blue;
    }
    h2 {
        @apply text-xl;
    }
}

@layer components {
    /* lot views */
    /* .bookmark-btn-holder path {
        stroke: #ecc94b;
        fill: #ecc94b;
    } */
    .directions-button {
         @apply float-right px-4 py-2 ml-3 font-bold text-white bg-blue-500 border-2 border-blue-500 rounded hover:bg-blue-700;
    }
    .lot-card-text-info {
        @apply text-gray-500;
    }
    .bookmark-button-true{
        @apply float-right px-4 py-2 ml-3 font-bold text-white bg-yellow-500 border-2 border-yellow-500 rounded hover:bg-yellow-500;
    }
    .bookmark-button-false{
        @apply float-right px-4 py-2 ml-3 font-bold text-yellow-500 bg-white border-2 border-yellow-500 rounded hover:bg-yellow-300 hover:text-white;
    }
    .permit-symbol::after{
        content: " "
    }
    .permit-symbol-custom{
        @apply text-xl font-extrabold text-[#f50000];
    }
    .permit-symbol-general{
        /* @apply font-bold text-purple-500; */
    }
    .spacer::after{
        content: "•"
    }
    /* lot list view */
    .lot-list{
        @apply w-full;
    }
    .lot-link {
        @apply italic text-gray-400;
    }
    .lot-name{
        @apply text-xl;
    }
    .lot-card-text-info {
        @apply text-gray-500
    }
    /* single lot view */
    .lot-card-single {
        @apply w-full;
    }
    .lot-card-image {
        @apply rounded-t-xl h-72;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
    }
    .lot-card-single-content {
        @apply p-3;
    }
    .lot-fullness{
        @apply flex text-center text-white bg-red-500 rounded-full aspect-square;
    }
    .lot-card-single-pass-info{
        @apply float-right col-span-6 m-6 text-center text-gray-500 md:text-xl sm:text-base;
    }
    
  }

@layer components {
    /* Reference: https://github.com/twbs/bootstrap/pull/31955/ */
    ::file-selector-button {
      @apply text-gray-800 bg-gray-300 hover:bg-gray-200
        font-medium text-sm cursor-pointer
        border-0 border-r border-solid border-gray-300
        py-2.5 pl-8 pr-4;
      margin-inline-start: -1rem;
      margin-inline-end: 1rem;
    }
    ::-webkit-file-upload-button {
      @apply text-gray-800 bg-gray-300 hover:bg-gray-200
        font-medium text-sm cursor-pointer
        border-0 border-r border-solid border-gray-300
        py-2.5 pl-8 pr-4;
      margin-inline-start: -1rem;
      margin-inline-end: 1rem;
    }
  }