.admin-navbar,.admin-navbar *{
z-index: 2000;
}


/* Scrollbar Style*/
 *::-webkit-scrollbar {
    width: 10px;
}

 *{
    scrollbar-color: rgba(63, 68, 68, 0.515) rgba(217, 223, 223, 0.3); /* thumb and track color */
  scrollbar-width: thin;
}

 *::-webkit-scrollbar-track {
    border-radius: 10px;
    background-color: rgba(217, 223, 223, 0.3);
}

*::-webkit-scrollbar-thumb {
     border-radius: 10px;
     height: 20px;
    background-color: rgba(63, 68, 68, 0.515);
}