.info {
    padding: 3px;
    font: 14px/16px Arial, Helvetica, sans-serif;
    background-color: rgba(255,255,255,0.55);
    border-radius: 7px;
}

.legend {
    text-align: center;
    color: #555;
    transform: translate(-5px,55px);
    font-family: Poppins;
    font-size: small;
}

.leaflet-left{
    position:absolute;
    white-space: nowrap;
    left: 50%;
    transform: translate(-50%, 0%);
}